body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
blockquote{
    margin: 0;
    margin-bottom: .6em;
    padding: 0 1em;
    color: #6a737d;
    border-left: 0.25em solid #dfe2e5;
}
.logo {
  width: 200px;
  height: auto;
  float: left;
  margin-right: 30px;
}
.logo img {
  width: 100%;
}

.loginForm {
  width: 300px;
  margin: 50px auto;
  box-shadow: 1px 1px 1px 1px #ccc;
  padding: 40px 30px;
}
.publishForm {
  width: 80%;
  box-shadow: 1px 1px 1px 1px #ccc;
  padding: 40px 30px;
  margin: 50px auto;
}
.article-title {
  width: 100%;
  text-align: center;
  font-size: 30px;
}
.userInfo {
  text-align: center;
  margin-bottom: 20px;
  color: #999;
}
.article img{
  display: block;
  max-width: 100%;
  margin-bottom: .6em;
}
.article pre {
  background: rgba(99,99,99, 0.1);
  padding: 15px;
}
.ant-list-vertical .ant-list-item-meta-title {
  margin-top: 6px;
}
