.logo {
  width: 200px;
  height: auto;
  float: left;
  margin-right: 30px;
}
.logo img {
  width: 100%;
}

.loginForm {
  width: 300px;
  margin: 50px auto;
  box-shadow: 1px 1px 1px 1px #ccc;
  padding: 40px 30px;
}
.publishForm {
  width: 80%;
  box-shadow: 1px 1px 1px 1px #ccc;
  padding: 40px 30px;
  margin: 50px auto;
}
.article-title {
  width: 100%;
  text-align: center;
  font-size: 30px;
}
.userInfo {
  text-align: center;
  margin-bottom: 20px;
  color: #999;
}
.article img{
  display: block;
  max-width: 100%;
  margin-bottom: .6em;
}
.article pre {
  background: rgba(99,99,99, 0.1);
  padding: 15px;
}
.ant-list-vertical .ant-list-item-meta-title {
  margin-top: 6px;
}